import ip from "../../static/ipconfig";
import Cookies from 'js-cookie';
import {getToken,setToken,removeToken} from "../utils/auth";

const configure = {
    state:{
        HOST: 'http://'+ip.IPCONFIG+':8888',  //
        activeName: '',                  //
        showAside: false,               //
        loginIn: false,                 //
        isActive: false,                //
    },
    getters: {
        activeName: state => {
            let activeName = state.activeName
            if(!activeName){
                activeName = JSON.parse(window.sessionStorage.getItem('activeName'))
            }
            return activeName
        },
        showAside: state => {
            let showAside = state.showAside
            if(!showAside){
                showAside = JSON.parse(window.sessionStorage.getItem('showAside'))
            }
            return showAside
        },
        loginIn: state => {
            let loginIn = state.loginIn
            if(!loginIn){
                loginIn = JSON.parse(Cookies.get('loginIn')==null?false:true)
            }
            return loginIn
        },
        isActive: state => {
            let isActive = state.isActive
            if(!isActive){
                isActive = JSON.parse(window.sessionStorage.getItem('isActive'))
            }
            return isActive
        }
    },
    mutations: {
        setActiveName: (state,activeName) => {
            state.activeName = activeName
            window.sessionStorage.setItem('activeName',JSON.stringify(activeName))
        },
        setShowAside: (state,showAside) => {
            state.showAside = showAside
            window.sessionStorage.setItem('showAside',JSON.stringify(showAside))
        },
        setLoginIn: (state,loginIn,token) => {
          state.loginIn = loginIn
           // tokenCookie token
          setToken(token)
          Cookies.set('loginIn', JSON.stringify(loginIn));
        },
        setIsActive: (state,isActive) => {
            state.isActive = isActive
            window.sessionStorage.setItem('isActive',JSON.stringify(isActive))
        }
    },
    actions:{
      // 
      LogOut({ commit, state }) {
        return new Promise((resolve, reject) => {
          logout(state.token).then(() => {
            removeToken()
            resolve()
          }).catch(error => {
            reject(error)
          })
        })
      },
    }
}

export default configure



